/* Overrides to ensure the feature image sizing rules are loaded
   and have sufficient specificity. This file compiles to
   /_styles/overrides.css and is linked in the site head. */

.feature[data-style="home"] .feature-image {
  width: 5% !important;
  max-width: none !important;
}

@media (max-width: 800px) {
  .feature[data-style="home"] .feature-image {
    width: 30% !important;
  }
}

.team-gallery .feature-image {
  width: 60% !important;
}

/* fallback: ensure default feature images are a sensible size */
.feature-image {
  width: 30% !important;
}
